<template>
  <div class="chartSetting">
    <!-- {{value}} -->
    <!-- <div id="editor-container"></div> -->
    <JsonEditor v-model="result"  @json-change="onJsonChange"></JsonEditor>
  </div>
</template>

<script>
import JsonEditor from 'vue-json-editor'
export default {
  name: 'chartSetting',
  props: {
    value: {
      require: true,
    },
  },
  data() {
    return {
        result:{}
    };
  },
  components: {JsonEditor},
  methods: {
    onJsonChange(value){
      this.$emit('update',value)
    }
  },

  watch: {
      value:{
          deep:true,
          immediate:true,
          handler(v){
              this.result=v
          }
      }
  },
};
</script>

<style lang='scss' scoped>
.chartSetting {
  width: 100%;
  overflow: scroll;
}
</style>