<template>
  <div>
    <div ref='main'></div>
  </div>
</template>

<script>
export default {
  name: 'autoSector',
  props: {
    value: {
      type: Object,
      require: true,
    },
  },
  data() {
    return {};
  },
  components: {},
  methods: {
    initChart() {
      const contain = this.$refs.main;
      const chart=this.$chat.init(contain)
      this.value&&chart.setOption(this.value)
    },
  },
  mounted() {
    
  },
  computed: {},
  watch: {
    value:{
      deep:true,
      immediate:true,
      handler(){
        this.$nextTick(()=>{
              this.initChart();
        })
      }
    }
  },
};
</script>

<style lang='scss' scoped>
div {
  height: 300px;
}
</style>