<template>
  <div>
    <el-select v-model="value.value">
      <el-option v-for="im in list" @blur="blur" :value="im.value" :key="im.value"></el-option>
    </el-select>
  </div>
</template>

<script>
export default {
  name: 'autoSelect',
  props: {
    value: {},
  },
  data() {
    return {
      list: [
        { label: 1, value: 1 },
        { label: 2, value: 2 },
        { label: 3, value: 3 },
        ],
    };
  },
 
  methods: {
      blur() {
      this.$emit('blur');
    },
  },
  mounted() {},
  computed: {},
  watch: {},
};
</script>

