<template>
  <div>
    <el-input type="text" v-if="!(value.trim||value.lazy)" @blur="blur" v-model.trim="value.value" :placeholder="value.placeholder" />
    <el-input type="text" v-if="value.number" @blur="blur" v-model.number="value.value" :placeholder="value.placeholder" />
    <el-input type="text" v-if="value.lazy" @blur="blur" v-model.lazy="value.value" :placeholder="value.placeholder" />
  </div>
</template>

<script>
export default {
  name: 'autoInput',
  props: {
    value: {
      type: Object,
      require: true,
    },
  },
  data() {
    return {};
  },
  methods: {
    blur() {
      this.$emit('blur');
    },
  },
  mounted() {},
  computed: {},
  watch: {},
};
</script>

