/* eslint-disable no-useless-catch */
const upperFirst = (str) => {
    let newStr = ''
    try {
        for (let i = 0; i < str.length; i++) {
            if (i === 0) {
                newStr += str[i].toUpperCase()
            } else {
                newStr += str[i]
            }
        }
        return newStr
    } catch (error) {
        throw (error)
    }
}

const debounce = () => {
    let time = null
    return function(fn, params, delay = 2000)  {
        if (time) {
            clearTimeout(time)
        }
        time = setTimeout(() => {
            fn(params)
        }, delay);
    }
}

const throttle=()=>{
    let time=null
    return function(fn, params, delay = 2000){
        if(!time){
            time = setTimeout(() => {
                fn(params)
                time=null
                clearTimeout(time)
            }, delay); 
        }
    }
}


module.exports = {
    upperFirst,
    debounce:debounce(),
    throttle:throttle()
}